.correction-attente-container {
    padding: 1.5rem;

    .header-section {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 1.5rem;
        border-bottom: 1px solid var(--surface-border);

        .page-title {
            margin: 0;
            font-size: 1.5rem;
            font-weight: 600;
            color: var(--text-color);
            display: flex;
            align-items: center;
            gap: 0.75rem;

            i {
                color: var(--primary-color);
                font-size: 1.3rem;
            }
        }

        .header-actions {
            display: flex;
            gap: 1rem;
            align-items: center;

            .search-input {
                width: 300px;
            }
        }
    }

    .loading-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 3rem;
        gap: 1rem;

        p {
            color: var(--text-color-secondary);
            font-size: 1.1rem;
        }
    }

    .error-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 3rem;
        gap: 1rem;

        i {
            font-size: 3rem;
            color: var(--red-500);
        }

        p {
            color: var(--text-color);
            font-size: 1.1rem;
            margin: 0.5rem 0;
        }
    }

    .table-container {
        .table-caption {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0.5rem 0;

            span {
                color: var(--text-color-secondary);
                font-weight: 500;
            }
        }

        .code-badge {
            background: var(--primary-100);
            color: var(--primary-700);
            padding: 0.25rem 0.5rem;
            border-radius: 4px;
            font-weight: 600;
            font-size: 0.875rem;
            font-family: monospace;
        }

        .client-info {
            strong {
                display: block;
                color: var(--text-color);
                margin-bottom: 0.25rem;
            }

            .text-muted {
                color: var(--text-color-secondary);
                font-size: 0.875rem;
            }
        }

        .id-info {
            span {
                font-weight: 500;
                color: var(--text-color);
            }

            .text-muted {
                color: var(--text-color-secondary);
                font-size: 0.75rem;
                margin-top: 0.25rem;
            }
        }

        .phone-info {
            i {
                color: var(--primary-color);
                margin-right: 0.25rem;
                font-size: 0.875rem;
            }

            .text-muted {
                color: var(--text-color-secondary);
                font-size: 0.875rem;
                margin-top: 0.25rem;
            }
        }

        .agence-code {
            background: var(--surface-100);
            padding: 0.25rem 0.5rem;
            border-radius: 4px;
            font-weight: 500;
            font-size: 0.875rem;
        }

        .action-buttons {
            display: flex;
            gap: 0.25rem;
            justify-content: center;

            button {
                &:hover {
                    transform: scale(1.1);
                    transition: transform 0.2s ease;
                }
            }
        }

        .empty-message {
            padding: 2rem;
            text-align: center;

            i {
                color: var(--text-color-secondary);
                margin-bottom: 1rem;
            }

            p {
                color: var(--text-color-secondary);
                font-size: 1.1rem;
                margin: 0;
            }
        }
    }

    .summary-section {
        padding: 1rem 0;
        border-top: 1px solid var(--surface-border);

        .summary-stats {
            display: flex;
            gap: 2rem;
            align-items: center;

            .stat-item {
                display: flex;
                align-items: center;
                gap: 0.5rem;
                color: var(--text-color-secondary);

                i {
                    color: var(--primary-color);
                    font-size: 0.875rem;
                }

                strong {
                    color: var(--text-color);
                    font-weight: 600;
                }
            }
        }
    }
}

// Responsive adjustments
@media (max-width: 768px) {
    .correction-attente-container {
        padding: 1rem;

        .header-section {
            flex-direction: column;
            gap: 1rem;

            .page-title {
                font-size: 1.2rem;
            }

            .header-actions {
                width: 100%;
                justify-content: space-between;

                .search-input {
                    width: 100%;
                    max-width: 250px;
                }
            }
        }

        .summary-section {
            .summary-stats {
                flex-direction: column;
                gap: 0.5rem;
                align-items: flex-start;
            }
        }
    }
}

// Dark mode support
@media (prefers-color-scheme: dark) {
    .correction-attente-container {
        .code-badge {
            background: var(--primary-900);
            color: var(--primary-100);
        }

        .agence-code {
            background: var(--surface-700);
            color: var(--text-color);
        }
    }
}

// Print styles
@media print {
    .correction-attente-container {
        .header-actions,
        .action-buttons {
            display: none;
        }

        .table-container {
            font-size: 0.875rem;
        }
    }
}

.inactive-agent-warning {
    margin: 2rem;
    background: #fee;
    border: 2px solid #fcc;
    border-radius: 8px;
    padding: 2rem;

    .warning-content {
        display: flex;
        gap: 1.5rem;
        align-items: flex-start;

        .warning-icon {
            flex-shrink: 0;

            i {
                font-size: 3.5rem;
                color: #dc3545;
            }
        }

        .warning-details {
            flex: 1;

            .warning-title {
                font-size: 1.5rem;
                font-weight: 700;
                color: #c82333;
                margin: 0 0 1rem 0;
            }

            .warning-messages {
                .primary-message {
                    font-size: 1.1rem;
                    font-weight: 600;
                    color: #721c24;
                    margin-bottom: 0.75rem;
                }

                .secondary-message {
                    font-size: 1rem;
                    color: #721c24;
                    margin-bottom: 1rem;
                    line-height: 1.6;
                }

                .action-steps {
                    background: white;
                    border: 1px solid #f5c6cb;
                    border-radius: 6px;
                    padding: 1rem;
                    margin: 1rem 0;

                    h4 {
                        color: #721c24;
                        font-weight: 600;
                        margin: 0 0 0.75rem 0;
                    }

                    ul {
                        list-style: disc;
                        padding-left: 1.5rem;
                        margin: 0;

                        li {
                            color: #721c24;
                            margin-bottom: 0.5rem;
                            line-height: 1.5;
                        }
                    }
                }

                .info-note {
                    background: #fff3cd;
                    border: 1px solid #ffc107;
                    border-radius: 6px;
                    padding: 0.75rem;
                    margin-top: 1rem;

                    p {
                        margin: 0;
                        color: #856404;
                        font-size: 0.9rem;

                        i {
                            margin-right: 0.5rem;
                            color: #ffc107;
                        }

                        strong {
                            color: #856404;
                        }
                    }
                }
            }

            .warning-actions {
                display: flex;
                gap: 1rem;
                margin-top: 1.5rem;
            }
        }
    }
}
